<template>
  <div class="">
		<div class="menu-placeholder"><!-- 底部菜单占位 --></div>
    <ul class="flex-box mune border-top bg-f">
				<router-link to="/" tag="li" class="item">
					<input type="radio" name="tab" id="find" checked/>
					<label for="find" class="find"></label>
				</router-link>
				<router-link to="/enrollList" tag="li" class="item">
					<input type="radio" name="tab" id="enroll"/>
					<label for="enroll" class="enroll"></label>
				</router-link>
				<router-link to="/bespoke" tag="li" class="item">
					<input type="radio" name="tab" id="1"/>
					<label for="1"><div class="center-btn" ></div ></label>
				</router-link>
				<router-link to="/today" tag="li" class="item">
					<input type="radio" name="tab" id="today"/>
					<label for="today" class="today"></label>
				</router-link>
				<router-link to="/person" tag="li" class="item">
					<input type="radio" name="tab" id="person"/>
					<label for="person" class="person"></label>
				</router-link>
		</ul>
  </div>
</template>
<script>
export default {
  name: 'bottomMenu',
  data () {
    return {
      
    }
  }
}
</script>
<style scoped lang="scss">
	.menu-placeholder{
		height: 50px;
	}
	.mune{
		position: fixed;
		bottom: 0;
		width: 100%;
		font-size: $font-size / 2;
		z-index:6;
		
		li{
			position: relative;
			padding: 11px 0 5px 0;
		}
		
	}
	label{
		display: block;
		height: 33px;
	}
	
	/* 图片做背景 */
	.find{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%; 
	}
	.enroll{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%; 
	}
	.today{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%; 
	}
	.person{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%; 
	}
	.center-btn{
		position: relative;
		top: -18px;
		left: 50%;
		margin-left: -25px;
		width: 49px;
		height: 49px;
		border-radius: 50%;
		box-shadow: 0 0 2px $main-color;
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 33px;
		z-index: 2;
	}
	
	
	input[type="radio"]:checked+.find{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%;
	}
	input[type="radio"]:checked+.enroll{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%;
	}
	input[type="radio"]:checked+.today{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%;
	}
	input[type="radio"]:checked+.person{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 100%;
	}
	input[type="radio"]:checked+label .center-btn{
		background:#fff url(data:image/gif;base64,) no-repeat center;
		background-size: auto 33px;
	}
</style>



